          <div class="articleCardShop">
            <span>{{ item.itemShop }}</span>
          </div>
        </div>

        <div class="articleCardContent">
          <div>{{ item.itemDes }}</div>
          <div>{{ item.itemPrice }}</div>
        </div>
      </div>
    </div>
    <div v-if="mobileSize == 0" class="rank">
