            <div class="rankItemDes">{{ item.price }}</div>
          </div>
        </div>
      </div>
    </div>
    <div v-if="mobileSize == 0" class="articleList">
      <div class="articleListHead"></div>
      <div
        v-for="item in rank1"
        :key="item.itemTitle + '6'"
