          this.rankData = this.rank
          this.rankSliderIndex = 0
        }
      } else {
        if (this.rankData != this.rank1) {
          this.rankData = this.rank1
          this.rankSliderIndex = 0
        }
      }
      this.handleRankSliderSize()
    },
