    overflow: hidden;
    white-space: nowrap;
    margin: 0 20px;
    height: 300px;
    padding-top: 20px;
    .leftButton {
      position: absolute;
      top: calc(35% - 12px);
      left: 0;
      z-index: 1;
      display: none;
    }
    .rightButton {
      position: absolute;
      top: calc(35% - 12px);
      right: 0;
      z-index: 1;
      display: none;
    }
